body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
  background-color: #282c34;
  color: white;
}

.App > div {
  outline: gray dotted 0.2vmin;
  min-width: 60vw;
  max-width: 60vw;
  margin-bottom: 4vmin;
  padding: 2vmin;
  border-right: solid 2vmin rgba(0, 0, 0, 0);
}

.App ul {
  overflow: hidden;
}

.App ul > li {
  white-space: nowrap;
}

.App ol {
  overflow: hidden;
}

.App ol > li {
  white-space: nowrap;
}

.App div > h2 {
  margin-top: 0
}

ul:last-child {
  margin-bottom: 0
}

h3 > pre {
  display: inline;
  margin: 0;
}
